.widget{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
    box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
    border-radius: 10px;
    height: 100px;
}
.widget .left , .widget .right{
display: flex;
flex-direction: column;
justify-content: space-between;
}
.widget .left .title{
    font-weight: bold;
    font-size: 14px;
    color: rgb(160, 160, 160);
}
.widget .left .counter{
    font-size: 28px;
    font-weight: 300;
}
.widget .left .link{
    width: max-content;
    font-size: 12px;
    border-bottom: 1px solid gray;
}
.widget .right .percentage{
   display: flex;
   align-items: center;
   font-size: 14px; 
}
.widget .right .percentage.positive{
    color: green;
}
.widget .right .percentage.negative{
    color: red;
}
.right .icon{
    font-size: 25px;
    padding: 5px;
    border-radius: 5px;
    align-self: flex-end ;
}